<template>
  <div class="home">
    <input type="text" class="search" v-model="searchKeyword" placeholder="請輸入商品名稱">
    <div class="products-cards-container">
      <ProductDescriptionDrawer
      :product="product"
      :active="active.product_drawer"
      @close-product-drawer="closeProductDrawer()"
      />
      <ProductSummaryCard
        v-for="product in searchItems"
        :key="product.id"
        :product="product"
        @view-product="viewProduct($event)"
        />
    </div>
  </div>
</template>

<script>
import items from '../data/items.js'
import ProductSummaryCard from '../components/products/ProductSummaryCard.vue';
import ProductDescriptionDrawer from '../components/products/ProductDescriptionDrawer.vue';

export default {
  name: 'HomeView',
  components: {
    ProductSummaryCard, 
    ProductDescriptionDrawer,
    //CategoryTop,
  },
  data(){
    return{
      items: items,
      product: null,
      category_top: [],
      active: {
        product_drawer: false,
      },
      searchKeyword: null,
    }
    },
  computed: {
    searchItems() {
      if (this.searchKeyword) {
          return this.items.filter((e) => {
            return e.name.includes(this.searchKeyword);
        })
      } else {
        return this.items;
      }
    }
  },
  methods:{
    viewProduct(product) {
      this.product = product;
      this.active.product_drawer = true
    },
    closeProductDrawer() {
      this.active.product_drawer = false;
    },
    //將單一類別商品塞入一個陣列中
    findCategoryTop() {
      this.items.forEach(object => {
        if (object.category == "Top") this.category_top = object;
      });
    },
  },
}
</script>

<style lang="scss">
  .products-cards-container{
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
  }
  .search {
    width: 60vw;
    height: 50px;
    font-size: 20px;
  }

</style>
